import Vue from 'vue';
import { Log } from '@/utils/log';
import './lazy.css';
const observer = new IntersectionObserver((entries) => {
  entries.map((img) => {
    if (!img.isIntersecting) {
      return;
    }
    if (img.target instanceof HTMLImageElement) {
      setTimeout(() => {
        if (img.target instanceof HTMLImageElement) {
          img.target.src = img.target.getAttribute('data-src')!;
          img.target.classList.remove('lazy');
          img.target.classList.add('animation');
        }
      }, 500);
    }
  });
  },
);

Vue.directive('lazy', {
  bind(
    el: HTMLElement,
  ) {
    Log.log('bind', el.querySelectorAll('img'));
  },
  inserted(el: HTMLElement) {
    Log.log('inserted');
  },
  update(
    el: HTMLElement,
  ) {
    const imgs = el.querySelectorAll('img')!;
    Log.log('update', imgs);
    imgs.forEach((img) => {
      Log.log(img.src);
      img.setAttribute('data-src', img.src);
      img.src = '';
      img.classList.add('lazy');
      observer.observe(img);
    });
  },
  unbind(
    el: HTMLElement,
  ) {
    const imgs = el.querySelectorAll('img')!;
    Log.log('unbind', imgs);
    imgs.forEach((img) => {
      observer.unobserve(img);
    });
  },
});
